import React, { useState, useEffect } from 'react';

const fetchCarsData = async () => {
  try {
    const response = await fetch('/data.json');
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    return await response.json();
  } catch (error) {
    console.error('Error fetching car data:', error);
    return []; // Handle the error appropriately
  }
};

const CarList = () => {
  const [carsData, setCarsData] = useState([]);
  const [highlightedCars, setHighlightedCars] = useState(() => {
    const saved = localStorage.getItem('highlightedCars');
    return saved ? JSON.parse(saved) : [];
  });

  useEffect(() => {
    const getData = async () => {
      const data = await fetchCarsData();
      setCarsData(data);
    };

    getData();
  }, []);

  const toggleHighlight = (car) => {
    setHighlightedCars(prevState => {
      const isHighlighted = prevState.find(c => c.model === car.model);
      const newState = isHighlighted
        ? prevState.filter(c => c.model !== car.model)
        : [...prevState, car];

      localStorage.setItem('highlightedCars', JSON.stringify(newState));
      return newState;
    });
  };

  return (
    <div>
      <h1>Car List</h1>
      <div>
        {carsData.map((brandData, brandIndex) => (
          <div key={brandIndex}>
            <h2>{brandData.brand}</h2>
            <ul>
              {brandData.models.map((car, index) => (
                <li key={index} style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
                  <img
                    src={car.image}
                    alt={car.model}
                    style={{ width: '100px', height: 'auto', marginRight: '15px' }}
                  />
                  <div>
                    <span>
                      {car.model} - {car.numberOfCars} cars - {car.valueInBaht} Baht
                    </span>
                    <button
                      onClick={() => toggleHighlight(car)}
                      style={{
                        backgroundColor: highlightedCars.find(c => c.model === car.model) ? 'green' : 'grey',
                        color: 'white',
                        marginLeft: '10px',
                        padding: '5px 10px',
                        border: 'none',
                        cursor: 'pointer'
                      }}
                    >
                      {highlightedCars.find(c => c.model === car.model) ? 'Unhighlight' : 'Highlight'}
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        ))}
      </div>

      <h2>Highlighted Cars</h2>
      <table border="1">
        <thead>
          <tr>
            <th>Model</th>
            <th>Value (Baht)</th>
            <th>Image</th>
          </tr>
        </thead>
        <tbody>
          {highlightedCars.map((car, index) => (
            <tr key={index}>
              <td>{car.model}</td>
              <td>{car.valueInBaht}</td>
              <td>
                <img
                  src={car.image}
                  alt={car.model}
                  style={{ width: '100px', height: 'auto' }}
                />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CarList;
