import React, { useState, useEffect } from 'react';

const CarTable = () => {
  const [cars, setCars] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('/data.json'); // Adjust the path if necessary
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        setCars(data);
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div>
      <h1>Dashboard</h1>
      <table>
        <thead>
          <tr>
            <th>Brand</th>
            <th>Model</th>
            <th>Number of Cars</th>
            <th>Value (Baht)</th>
          </tr>
        </thead>
        <tbody>
          {cars.map((brandData, index) => (
            brandData.models.map((modelData, modelIndex) => (
              <tr key={`${index}-${modelIndex}`}>
                <td>{brandData.brand}</td>
                <td>{modelData.model}</td>
                <td>{modelData.numberOfCars}</td>
                <td>{modelData.valueInBaht.toLocaleString()}</td>
              </tr>
            ))
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CarTable;
